.header_logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 10px;
}

.header_search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header_searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: red;
}

.header_optionLineOne {
    font-size: 10px;
}

.header_optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header_nav {
    display: flex;
    justify-content: space-evenly;
}

.header_option {
    display: flex;
    text-decoration: none;
    color: black;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header_optionBasket {
    display: flex;
    align-items: center;
}

.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}

@media (max-width: 767px) {
    .header {
        height: 60px;
        display: block;
        align-items: center;
        background-color: rgba(206, 154, 154, 0.397);
        position: sticky;
        top: 0;
        width: 100vw;
        z-index: 100;
    }
    .header_logo {
        padding-left: 25%;
        width: 80px;
        object-fit: contain;
        margin: 0 20px;
        margin-top: 5px;
    }
    .header_option {
        color: white;
    }
    .header_search {
        margin-bottom: 10px;
        width: 100vw;
    }
    .header_searchInput {
        height: 12px;
        padding: 10px;
        border: none;
        width: 100vw;
    }
}